:global(::-webkit-scrollbar) {
  width: 6px;
  height: 6px;
}

:global(::-webkit-scrollbar-track) {
  -webkit-box-shadow: inset 0 0 6px #1976d2;
  border-radius: 10px;
}

:global(::-webkit-scrollbar-thumb) {
  border-radius: 10px;
  background-color: #1976d2;
}

:global #g_a11y_announcement {
  display: none;
}